.favorites__container {
    height: 288px;
    & > * {
        border-bottom: 1px solid rgba(135, 135, 135, 0.2);
        &:last-child {
            border: none;
            padding-bottom: 6px;
        }
    }
    overflow-y: auto;
    padding-right: 15px;
    width: 100%;
    min-width: 296px;

}
.favorites__title {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
    margin-bottom: 32px;
}
.favorites__btns {
    display: flex;
    & > * {
        margin-right: 8px;
        &:last-child {
            margin: 0;
        }
    }
    margin-bottom: 32px;
}
.btn {
    cursor: pointer;
    padding: 4px 4px 4px 8px;
    display: flex;
    max-width: 100px;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    box-sizing: border-box;
    border-radius: 4px;
    border: 1px solid #41522e;

    &.dis {
        color: #99a0a3;
        box-sizing: border-box;
        border-radius: 4px;
        border: 1px solid #e5e5e5;
    }
}
.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 18px;
    margin-left: 4px;
    opacity: 1;
    transform: rotate(180deg);
    &.dis {
        opacity: 0.3;
    }
    &.reverse {
        transform: rotate(0deg);
    }
}
.hotelcard__wrapper {
    padding: 16px 0;
    width: 296px;
}
